.main_why-dubai {
  margin: 0;

  .section-structuring {
    padding: 56px 0;
    @media (min-width: 821px) {
      padding: 160px 0;
    }

    &--v2 {
      .section-title {
        grid-column: span 6;
      }

      .block-text {
        grid-column: 7/-1;
      }

      .block-cards {

        &__cards {
          counter-reset: card;
          background-color: #ccc;
          grid-gap: 0;
          grid-auto-rows: 148px;
        }

        .card {
          grid-column: span 6;
          border-radius: 0;
          box-shadow: none;
          background-color: transparent;
          padding: 0;

          &__in {
            padding-left: 106px;
            transition: all $dur ease;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
          }

          &:hover {

            .card__in {
              background-color: $blackColor;
            }

            .card__title,
            &::after {
              color: $lightGreen;
            }

            &::before {
              opacity: 0;
            }
          }

          &::before, &::after {
            transition: all $dur ease;
          }

          &::before {
            top: inherit;
            bottom: 0;
            left: 15px;
            height: 1px;
            background-color: $blackColor;
            width: calc(100% - 30px);
          }

          &::after {
            counter-increment: card;
            content: counter(card, decimal-leading-zero);
            position: absolute;
            top: 24px;
            left: 24px;
            font-size: 24px;
            line-height: calc(36 / 24);
            font-weight: 300;
          }
        }
      }

      @media (min-width: 821px) {
        .block-cards {
          &__cards {
            &.cards-even {
              .card {
                &:nth-last-child(-n+2) {
                  &::before {
                    display: none;
                  }
                }
              }
            }

            &.cards-odd {
              .card {
                &:last-child {
                  &::before {
                    display: none;
                  }
                }
              }
            }
          }
        }
      }

      @media (max-width: $md) {
        padding: 60px 0;

        .block-text {
          margin-bottom: 36px;
        }

        .block-cards {

          &__cards {
            grid-auto-rows: 94px;
          }

          .card {
            grid-column: span 12;

            &::after {
              font-size: 20px;
              line-height: calc(30/20);
              top: 12px;
              left: 16px;
            }

            &:last-child {
              &::before {
                display: none;
              }
            }

            &__in {
              padding-left: 58px;
            }
          }
        }
      }
    }
  }

  .section-offers {
    padding: 80px 0 140px;
    background-color: #323b40;

    .section-heading {
      .section-title {
        grid-column: span 7;
      }

      p {
        grid-column: 8/-1;
        text-align: right;
        font-size: 24px;
        line-height: (29/24);
        color: #808689;
        font-family: $sf;
      }
    }

    .section-body {
      .offers {
        &::before,
        &::after {
          background-color: #808689;
        }

        &__item {
          border-color: #808689;
        }
      }
    }

    @media (max-width: $md) {
      padding: 48px 0 86px;
      .section-heading {
        margin-bottom: 28px;

        .section-title {
          padding: 0;
          margin-bottom: 16px;
        }

        p {
          text-align: left;
          font-size: 20px;
          line-height: (24/20);
          margin: 0;
        }
      }
    }
  }
}
