.next-service {
  padding: 66px 0;
  background-color: $blackColor;

  p {
    color: #808689;
    font-size: 32px;
    line-height: (38/32);
    font-family: $sf;
    display: flex;
    align-items: center;

    a {
      color: $lightGreen;
      margin-left: 36px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      transition: color $dur ease;

      &:hover {
        color: $green;
        &::after {
          transform: translateX(5px) rotate(-90deg);
          filter: invert(43%) sepia(91%) saturate(1000%) hue-rotate(57deg)
            brightness(96%) contrast(103%);
        }
      }

      &::after {
        content: '';
        background-image: url(/img/sprite.svg#double-chevron);
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        width: 48px;
        height: 48px;
        transform: rotate(-90deg);
        transition: all $dur ease;
        flex-shrink: 0;
        filter: invert(20%) sepia(69%) saturate(371%) hue-rotate(39deg)
          brightness(98%) contrast(89%);
      }
    }
  }

  @media (max-width: $sm) {
    padding: 32px 0 55px;

    p {
      flex-wrap: wrap;
      font-size: 24px;
      line-height: (29/24);

      a {
        margin: 12px 0 0 0;
        align-items: flex-start;

        &::after {
          width: 40px;
          height: 40px;
        }
      }
    }
  }
}
