.section-make-remarkable {
  padding: 0 !important;
  .section-in {
    @include grid(12);
    grid-template-rows: 280px;
  }

  .block-text {
    grid-column: 2 / 8;
    border-right: 1px solid $lightGreyColor;
    display: flex;
    align-items: center;

    p {
      max-width: 670px;
      margin-right: 30px;
    }
  }

  .block-link {
    grid-column: 8 / -1;
    text-align: center;
    align-self: center;

    a {
      transition: color $dur ease;

      &:hover {
        color: $green !important;
      }
    }
  }

  .block-text p,
  .block-link a {
    font-size: 36px;
    font-family: $sf;
    line-height: (40/36);
    color: $blackColor;
  }

  @media (max-width: $xxl) {
    .block-text {
      grid-column: 1/8;
    }
  }

  @media (max-width: $md) {
    .section-in {
      grid-template-rows: auto;
      width: calc(100% + 32px);
      margin-left: -16px;
      grid-gap: 0;
    }

    .block-text,
    .block-link {
      grid-column: span 12;
    }

    .block-text p,
    .block-link a {
      font-size: 24px;
      line-height: (26/24);
    }

    .block-text {
      border: 0;
      padding: 48px 16px;
      display: block;
      text-align: center;

      p {
        margin: 0;
        max-width: 100%;
      }
    }

    .block-link {
      padding: 41.5px 16px;
      background-color: #323b40;

      a {
        color: $lightColor;
      }
    }
  }
}
