.footer {
  background-color: #e7e7e7;
  padding: 68.5px 0 38.5px;
  position: relative;

  .go-top-button {
    position: absolute;
    right: 20px;
    top: 64px;
  }

  &-in {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-row-gap: 56px;
    font-size: 22px;
    line-height: (30/22);
    font-family: $sf;

    a {
      color: inherit;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &-logo {
    grid-column: 1/6;

    svg {
      width: 231px;
      height: 47px;
    }
  }

  &-contact {
    &--contacts {
      grid-column: span 3;
    }

    &--address {
      grid-column: span 4;
    }

    &__title {
      font-weight: 600;
      margin-bottom: 1px;
    }
  }

  &-socials {
    grid-column: 9 / -1;

    ul {
      display: flex;
      flex-wrap: wrap;
      margin: -8px -10px;
    }

    li {
      margin: 8px 10px;
    }

    a {
      font-weight: 600;
    }
  }

  &-copyright {
    grid-column: span 12;
    display: grid;
    grid-template-columns: repeat(12, 1fr);

    p {
      &:first-child {
        grid-column: span 8;
      }
      &:last-child {
        grid-column: span 4;
      }

      a {
        text-decoration: underline;

        &:hover {
          text-decoration: none;
        }
      }
    }
  }

  @media (max-width: $lg) {
    &-in {
      font-size: 20px;
    }
  }

  @media (max-width: $md) {
    padding: 40px 0;

    .go-top-button {
      right: 16px;
      top: 37px;
    }

    &-in {
      grid-template-columns: 1fr;
      grid-gap: 40px;
      font-size: 16px;
      line-height: (22/16);

      * {
        grid-column: auto;
      }
    }

    &-logo {
      svg {
        width: 164px;
        height: 33px;
      }
    }

    &-contact {
      &__title {
        margin-bottom: 8px;
      }
    }

    &-copyright {
      p {
        &:last-child {
          grid-column: -1;
        }
      }
    }
  }

  @media (max-width: $sm) {
    &-copyright {
      grid-template-columns: 1fr;
      grid-gap: 16px;

      p {
        &:first-child,
        &:last-child {
          grid-column: auto;
        }
      }
    }
  }
}
