.breadcrumbs {
  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: -3px;
  }

  &__item {
    margin: 3px;
  }

  &__link,
  &__current {
    font-size: 18px;
    line-height: 1;
    font-family: $sf;
  }

  &__link {
    color: #fff;
    transition: color $dur ease;

    &::after {
      content: '>';
      margin-left: 6px;
    }

    &:hover {
      color: $lightGreen;
    }
  }

  &__current {
    color: #ccc;
  }

  @media (max-width: $md) {
    &__link,
    &__current {
      font-size: 16px;
    }
  }

  @media (max-width: $sm) {
    display: none;
  }
}
