.styled-btn {
  a,
  button {
    height: 60px;
    display: inline-flex;
    align-items: center;
    border: 1px solid $greyColor;
    font-family: $sf;
    color: $greyColor;
    transition: all $dur ease;
		cursor: pointer;

    &:hover {
      border-color: $lightGreen;
      background-color: $lightGreen;
      padding: 10px 0;
      color: $blackColor;

      .styled-btn__arrow {
        svg {
          opacity: 1;
        }
      }
    }
  }

  &__text {
    font-size: 20px;
    padding: 0 16px;
  }

  &__arrow {
    width: 60px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 1px solid $greyColor;
    transition: border $dur ease;

    svg {
      transform: rotate(90deg);
      width: 18px;
      height: 11px;
      opacity: 0.5;
    }
  }

  &--white {
    a,
    button {
      color: #fff;
      border-color: #fff;

      &:hover {
        .styled-btn__arrow {
          border-left-color: $blackColor;
          svg {
            filter: none;
          }
        }
      }
    }

    .styled-btn__arrow {
      border-left-color: #fff;
      svg {
        opacity: 1;
        filter: brightness(0) invert(1);
      }
    }
  }

  &--v2 {
    a,
    button {
      height: auto;
      border: none;
      font-size: 24px;
      color: $blackColor;
      font-weight: 600;
			background-color: transparent;

      &:hover {
        border-color: inherit;
        background-color: inherit;
        padding: inherit;
        color: inherit;

        .styled-btn__arrow {
          background-color: $lightGreen;
        }
      }
    }

		.styled-btn__text {
			padding: 0;
		}

    .styled-btn__arrow {
      border-left: inherit;
      width: auto;
      margin-left: 8px;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background-color: transparent;
      transition: background-color $dur ease;

      svg {
        transform: rotate(90deg);
        width: 12px;
        height: 8px;
        opacity: inherit;
      }
    }
  }

  @media (max-width: $exSm) {
    a,
    button {
      width: 100%;
    }
    &__text {
      text-align: center;
      flex-grow: 1;
    }

    &--v2 {
      a,
      button {
        font-size: 20px;
        width: auto;
        .styled-btn__arrow {
          background-color: $lightGreen;
        }
      }
    }
  }
}
