.section-banner {
  height: 600px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  z-index: 1;

  &::before,
  &::after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
  }

  &::before {
    background-color: #00000052;
  }

  &::after {
    background: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.3) 0%,
      rgba(0, 0, 0, 0) 100%
    );
  }

  .container,
  .section-in {
    height: 100%;
  }

  .section-in {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 100px;
  }

  .section-title {
    color: #fff;
    overflow: hidden;
    visibility: hidden;
    opacity: 0;
    transform: translateX(50px);
    span {
      display: block;
    }
  }

  .breadcrumbs {
    visibility: hidden;
  }

  .breadcrumbs ~ .section-title {
    margin-top: 52px;
  }

  @media (max-width: $md) {
    height: 390px;

    .section-in {
      padding-bottom: 48px;
    }

    .breadcrumbs ~ .section-title {
      margin-top: 24px;
    }
  }
}
