.section-structuring {
  .section-in {
    @include grid(12);
    grid-gap: 80px 30px;
    overflow: hidden;
  }

  .section-title {
    grid-column: span 3;
    max-width: 292px;
  }

  .block-text {
    grid-column: span 8;

    p {
      margin-bottom: 36px;
      font-size: 32px !important;
      line-height: (45/36) !important;
      font-weight: 300;
      letter-spacing: -0.01em;

      &:last-child {
        margin: 0;
      }

      &.highlight,
      &:first-child {
        font-family: $sf;
        font-size: 48px !important;
        line-height: (55/48) !important;
      }
    }
  }

  .block-img {
    grid-column: 4 / -2;
    height: 300px;
    overflow: hidden;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  .block-cards {
    grid-column: span 12;
    @include grid(12);
    grid-row-gap: 70px;

    &__cards {
      @include grid(12);
      grid-auto-rows: 200px;
      grid-column: span 12;
    }

    &__notice {
      grid-column: 6/-1;

      p {
        font-size: 28px;
        line-height: (39/28);
        font-weight: 300;
        margin-bottom: 24px;
        position: relative;
        padding-left: 26px;

        &::before {
          content: '';
          width: 10px;
          height: 10px;
          background-color: $green;
          position: absolute;
          left: 0;
          top: 14px;
        }

        &:last-child {
          margin: 0;
        }
      }
    }

    .card {
      grid-column: span 4;
      background-color: #e7e7e7;
      border-radius: 10px;
      overflow: hidden;
      position: relative;
      padding-left: 57px;
      display: flex;
      align-items: center;
      box-shadow: -1px 1px 0px rgba(0, 0, 0, 0.25);

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 0;
        transition: all $dur ease;
        background-color: $green;
        background: linear-gradient(
          to bottom,
          rgba(83, 165, 0, 1) 0%,
          rgba(83, 165, 0, 0.58) 50%,
          rgba(83, 165, 0, 0.49) 61%,
          rgba(83, 165, 0, 0.44) 67%,
          rgba(82, 165, 0, 0.16) 100%
        );
      }

      &:hover {
        &::before {
          width: 12px;
        }
      }

      &__title {
        font-weight: 300;
        font-size: 36px;
        line-height: (54/36);
      }
    }
  }

  @media (max-width: $xl) {
    .block-text {
      grid-column: span 9;
    }

    .block-cards {
      .card {
        grid-column: span 6;
      }
    }

    .block-img {
      grid-column: 4 / -1;
    }
  }
  @media (max-width: $md) {
    .section-in {
      display: block;
    }

    .section-title {
      margin-bottom: 36px;
    }

    .block-text {
      p {
        font-size: 20px !important;
        line-height: (30/20) !important;
        margin-bottom: 32px;

        &.highlight,
        &:first-child {
          font-size: 24px !important;
          line-height: (29/24) !important;
        }
      }
    }

    .block-img {
      margin-top: 32px;
      height: 200px;
    }

    .block-cards {
      grid-row-gap: 36px;

      &__cards {
        grid-auto-rows: 80px;
        grid-gap: 10px;
      }

      &__notice {
        grid-column: span 12;

        p {
          font-size: 18px;
          line-height: (23/18);

          &::before {
            top: 5px;
          }
        }
      }

      .card {
        grid-column: span 12;
        padding-left: 36px;

        &::before {
          width: 6px;
        }

        &:hover {
          &::before {
            width: 6px;
          }
        }

        &__title {
          font-size: 24px;
          line-height: (36/24);
        }
      }
    }
  }
}
