.section-info {
  .section-heading {
    margin-bottom: 72px;
  }

  .section-body {
    .info-container {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
    }

    .info-text {
      padding-bottom: 36px;
      border-bottom: 1px solid $lightGreyColor;

      p {
        font-size: 32px;
        font-weight: 300;
        line-height: (48/32);
        margin-bottom: 60px;
        text-align: justify;

        &:last-child {
          margin: 0;
        }
      }
    }

    .info-img {
      overflow: hidden;
      width: 100%;
      height: 478px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .info-text,
    .info-img,
    .styled-btn {
      grid-column: 4/-2;
    }
  }

  @media (max-width: $xxl), (max-height: $xxlH) {
    .section-body {
      .info-text {
        p {
          font-size: 24px;
          margin-bottom: 32px;
        }
      }
      .info-img {
        height: 345px;
      }
    }
  }

  @media (max-width: 1024px) {
    .section-body {
      .info-text,
      .info-img,
      .styled-btn {
        grid-column: 1/-1;
      }
    }
  }

  @media (max-width: $md) {
    .section-heading {
      margin-bottom: 24px;
    }

    .section-body {
      .info-container {
        grid-template-columns: 1fr;
      }

      .info-text {
        padding-bottom: 24px;

        p {
          font-size: 20px;
          line-height: (30/20);
          margin-bottom: 24px;
          text-align: left;
        }
      }

      .info-img {
        display: none;
      }
    }
  }
}
