.go-top-button {
  width: 48px;
  height: 48px;
  background-color: $blackColor;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color $dur ease;

  svg {
    transform: rotate(180deg);
    width: 18px;
    height: 18px;
    transition: filter $dur ease;
  }

  &:hover {
    background-color: $lightGreen;
    svg {
      filter: brightness(0%);
    }
  }

  @media (max-width: $md) {
    width: 40px;
    height: 40px;
  }
}
