.main_about {
  margin: 0;

  .section-structuring {
    padding-top: 200px;

    @media (max-width: $md) {
      padding-top: 56px;
    }
  }

  .section-how-we-approach {
    padding-top: 194px;

    @media (max-width: $md) {
      padding-top: 36px;
    }
  }

  .section-in-addition {
    padding-top: 0;
  }

  .section-our-values {
    padding: 118px 0 120px;

    .section-heading {
      margin-bottom: 56px;

      .section-title {
        text-align: center;
      }
    }

    .section-body {
      .values {
        @include grid(12);
        grid-gap: 0;
        border-top: 1px solid $lightGreyColor;
        border-bottom: 1px solid $lightGreyColor;
      }

      .value {
        grid-column: span 4;
        position: relative;
        padding: 56px 46px 36px;
        display: flex;
        flex-direction: column;

        &::before,
        &::after {
          content: '';
          position: absolute;
          background-color: $lightGreyColor;
        }

        &::before {
          right: 0;
          top: 10px;
          height: calc(100% - 20px);
          width: 1px;
        }

        &::after {
          bottom: 0;
          left: 15px;
          width: calc(100% - 30px);
          height: 1px;
        }

        &__name {
          font-size: 36px;
          line-height: (43/36);
          font-family: $sf;
          margin-bottom: 24px;
        }

        &__text {
          max-width: 460px;
          margin-bottom: 20px;

          p {
            font-size: 22px;
            font-weight: 300;
            line-height: (31/22);
          }
        }

        &__img {
          margin-top: auto;
          overflow: hidden;
          position: relative;
          max-width: 414px;
          width: 100%;
          position: relative;

          .cover {
            position: absolute;
            top: -1%;
            left: -1%;
            width: 103%;
            height: 103%;
            background-color: #fff;
            z-index: 1;
          }
        }
      }
    }

    @media (min-width: 821px) {
      .section-heading {
        .section-title {
          font-size: 64px;
          line-height: (70/64);
        }
      }

      .section-body {
        .styled-btn {
          display: none;
        }
      }
    }

    @media (min-width: 1281px) {
      .section-body {
        .value {
          &:nth-last-child(-n + 3) {
            &::after {
              display: none;
            }
          }

          &:nth-child(3n) {
            &::before {
              display: none;
            }
          }
        }
      }
    }

    @media (max-width: $xl) {
      .section-body {
        .value {
          grid-column: span 6;

          &:nth-last-child(-n + 2) {
            &::after {
              display: none;
            }
          }

          &:nth-child(2n) {
            &::before {
              display: none;
            }
          }
        }
      }
    }

    @media (max-width: $xl) {
      .section-body {
        .value {
          grid-column: span 6;

          &:nth-last-child(-n + 2) {
            &::after {
              display: none;
            }
          }

          &:nth-child(2n) {
            &::before {
              display: none;
            }
          }
        }
      }
    }

    @media (max-width: $md) {
      padding: 72px 0;

      .section-body {
        .values {
          padding: 40px 0 0 0;
          margin-bottom: 63px;
          border-bottom: 0;
        }

        .value {
          grid-column: span 12;
          padding: 0 0 24px 0;
          border-bottom: 1px solid $lightGreyColor;
          margin-bottom: 36px;
          display: none;

          &:nth-child(-n + 3) {
            display: block;
          }

          &:nth-child(3) {
            margin: 0;
            border-bottom: 0;
            padding: 0;
          }

          &:last-child {
            margin: 0;
            border-bottom: 0;
            padding-bottom: 0;
          }

          &::before,
          &::after {
            display: none;
          }

          &__name {
            font-size: 24px;
            line-height: (29/24);
          }

          &__text {
            max-width: 100%;
            margin-bottom: 24px;

            p {
              font-size: 20px;
              line-height: (30/20);
            }
          }
        }

        .styled-btn {
          &__arrow {
            transform: rotate(90deg);
          }
        }
      }

      &.expand {
        .section-body {
          .values {
            margin: 0;
          }
          .value {
            &:nth-child(3) {
              margin-bottom: 36px;
              border-bottom: 1px solid $lightGreyColor;
              padding-bottom: 24px;
            }
          }
        }
      }
    }
  }
}
