.header {
  background-color: $blackColor;
  border-bottom: 1px solid #808689;
  z-index: 5;
  position: sticky;
  top: 0;
  left: 0;
  transition: all $dur ease;

  &.scrolled {
    transform: translateY(-100%);
  }

  &.active {
    background-color: $lightColor;

    .header-logo {
      svg {
        fill: $blackColor;
      }
    }

    .header-burger {
      border-left-color: $blackColor;
      &::before,
      &::after {
        background-color: $blackColor;
      }
      &::before {
        margin: 0;
        transform: rotate(45deg);
      }
      &::after {
        transform: rotate(-45deg);
      }

      span {
        display: none;
      }
    }
  }

  .container {
    padding-right: 0;
    // max-width: 100%;
  }

  &-in {
    height: 90px;
    display: flex;
    justify-content: space-between;
  }

  &-logo {
    align-self: center;

    svg {
      height: 41px;
      width: 191px;
      fill: #fff;
      transition: fill $dur ease;
    }
  }

  &-burger {
    flex: 0 0 218px;
    border-left: 1px solid #cccccc80;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
    transition: border-color $dur ease;
    position: relative;
    overflow: hidden;

    &:hover {
      span {
        transform: none;
      }

      &::before,
      &::after {
        background-color: $blackColor;
      }
    }

    span {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $lightGreen;
      transform: translateY(100%);
      transition: transform $dur ease;
      z-index: -1;
    }

    &::before,
    &::after {
      content: '';
      width: 44px;
      height: 2px;
      background-color: #fff;
      transition: all $dur ease;
    }

    &::before {
      margin-bottom: 9px;
    }
  }

  @media (min-width: 2200px) {
    .container {
      max-width: 100%;
    }
  }

  @media (max-width: $md) {
    &-in {
      height: 70px;
    }

    &-logo {
      svg {
        width: 118px;
        height: 23px;
      }
    }

    &-burger {
      flex-basis: 80px;

      &::before {
        margin-bottom: 5px;
      }

      &::before,
      &::after {
        width: 24px;
        height: 1px;
      }
    }

    &.active {
      .header-burger {
        &::after {
          transform: translateY(-1px) rotate(-45deg);
        }
      }
    }
  }
}
