.main_service {
  margin: 0;

  .section-structuring {
    .block-text {
      p {
        &.highlight,
        &:first-child {
          max-width: 1130px;
        }
      }
    }

    &--v2 {
      padding-top: 160px;

      .block-text {
        grid-column: 6/-2;
      }
    }

    @media (max-width: $xl) {
      &--v2 {
        .block-text {
          grid-column: 6/-1;
        }
      }
    }

    @media (max-width: $md) {
      padding-top: 56px;

      &--v2 {
        .block-text {
          margin-bottom: 32px;
        }
      }
    }
  }

  .section-img-text {
    padding-top: 56px;
    @media (min-width: 821px) {
      padding-top: 180px;
    }
  }

  .section-strategic-adv {
    padding-top: 150px;

    @media (max-width: $md) {
      padding-top: 56px;
    }
  }

  .section-faq {
    padding: 98px 0 66px;

    @media (max-width: $md) {
      padding: 48px 0 32px;
    }
  }

  .section-offers {
    //margin-top: 194px;
    padding: 80px 0 140px;

    @media (max-width: $md) {
      //margin-top: 56px;
      padding: 48px 0 80px;
    }
  }
}
