.section-offers {
  background-color: $blackColor;

  .section-heading {
    margin-bottom: 48px;
    @include grid(12);
    align-items: baseline;

    .section-title {
      color: #fff;
      grid-column: span 5;
    }

    p {
      color: $lightGreen;
      grid-column: 6/-1;
      font-size: 24px;
      font-weight: 300;
      line-height: (34/24);
    }
  }

  .section-body {
    .offers {
      display: flex;
      flex-wrap: wrap;
      position: relative;
      &::before,
      &::after {
        content: '';
        position: absolute;
        height: 1px;
        width: 100%;
        background-color: #323b40;
        left: 0;
      }
      &::before {
        top: 0;
      }
      &::after {
        bottom: 0;
      }

      &--fill {
        .offers__item {
          flex-grow: 1;

          &:last-child {
            border-right: 0;
          }
        }
      }

      &__item {
        flex: 0 1 33.33%;
        display: flex;
        justify-content: center;
        border-bottom: 1px solid #323b40;
        border-right: 1px solid #323b40;
        padding: 100px 64px;

        &:nth-child(3n) {
          border-right: 0;
        }
      }

      .offer {
        text-align: center;
        max-width: 454px;

        &__img {
          margin-bottom: 24px;

          img,
          svg {
            margin: 0 auto;
            width: auto;
            height: 72px;
          }

          svg {
            fill: transparent;
          }
        }

        &__img,
        &__title,
        &__text {
          opacity: 0;
          visibility: hidden;
          transform: translateY(20px);
        }

        &__title,
        &__text {
          font-weight: 300;
        }

        &__title {
          font-size: 32px;
          line-height: (42/32);
          letter-spacing: 0.01em;
          color: $lightColor;
          margin-bottom: 16px;
        }

        &__text {
          p {
            color: #808689;
            font-size: 24px;
            line-height: (34/24);
            margin-bottom: 24px;

            &:last-child {
              margin: 0;
            }
          }
        }
      }
    }
  }

  @media (max-width: 1600px) {
    .section-body {
      .offers {
        &__item {
          padding-left: 16px;
          padding-right: 16px;
        }
      }
    }
  }

  @media (max-width: $xl) {
    .section-heading {
      .section-title {
        grid-column: span 7;
      }
      p {
        grid-column: span 5;
      }
    }
    .section-body {
      .offers {
        &__item {
          flex-basis: 50%;
          &:nth-child(3n) {
            border-right: 1px solid #323b40;
          }
          &:nth-child(2n) {
            border-right: 0;
          }
        }
      }
    }
  }

  @media (max-width: $lg) {
    .section-body {
      .offers {
        &__item {
          padding-left: 16px;
          padding-right: 16px;
        }
      }
    }
  }

  @media (max-width: $md) {
    .section-heading {
      display: block;
      margin-bottom: 32px;

      .section-title {
        margin-bottom: 32px;
        padding-bottom: 36px;
        border-bottom: 1px solid #323b40;
      }

      p {
        text-align: center;
        font-size: 20px;
        line-height: (28/20);
        // max-width: 309px;
        // margin: 0 auto;
      }
    }

    .section-body {
      .offers {
        &::after {
          display: none;
        }
        &__item {
          padding: 40px 0;
          border-right: 0 !important;
          flex-basis: 100%;

          &:last-child {
            border-bottom: 0;
            padding-bottom: 0;
          }
        }

        .offer {
          &__title,
          &__text {
            max-width: 100%;
          }

          &__title {
            font-size: 24px;
            line-height: (36/24);
          }

          &__text {
            p {
              font-size: 20px;
              line-height: (28/20);
              margin-bottom: 16px;
            }
          }
        }
      }
    }
  }

  @media (max-width: $sm) {
    .section-heading {
      p {
        max-width: 309px;
        margin: 0 auto;
      }
    }
  }
}
