.section-text-on-center {
  padding: 0 !important;

  .container {
    padding: 0 16px;
  }

  .section-in {
    @include grid(12);
    min-height: 510px;
    align-items: center;
    padding: 72px 0;
  }

  .block {
    grid-column: span 12;

    div {
      margin-bottom: 32px;

      &:last-child {
        margin: 0;
      }
    }

    p {
      font-size: 48px;
      line-height: (62/48);
      text-align: center;
      font-family: $sf;
      overflow: hidden;

      span {
        display: block;
      }
    }
  }

  &--bg {
    position: relative;

    .section-in {
      min-height: 595px;
      padding: 46px 0;
    }

    .block {
      grid-column: 3 / -3;
      p {
        color: $lightColor;
        line-height: (58/48);
      }

      &-bg {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: -1;
        background-size: cover;
        background-position: center;
        background-attachment: fixed;
        background-repeat: no-repeat;
      }
    }
  }

  @media (max-width: $xl) {
    .block {
      grid-column: 1 / -1;
    }
  }

  @media (max-width: $md) {
    .section-in {
      min-height: auto;
    }
    .block {
      grid-column: 1 / -1;

      div {
        margin-bottom: 24px;
      }

      p {
        font-size: 32px;
        line-height: (42/32);
      }
    }

    &--bg {
      .block {
        p {
          font-size: 24px;
          line-height: (31/24);
        }

        &-bg {
          background-position: center !important;
          background-attachment: initial;
        }
      }
    }
  }

  @media (max-width: $sm) {
    .block {
      text-align: center;
      p {
        display: inline;
        span {
          display: inherit;
        }
      }
    }
  }
}
