// FONTS
@mixin font($fname, $fstyle, $fweight, $furl) {
  @font-face {
    font-family: $fname;
    font-style: $fstyle;
    font-weight: $fweight;
    font-display: swap; // Uncomment to initially display system fonts
    src: local($fname), url($furl) format('woff2');
  }
}

@include font('Mukta', normal, 400, '../fonts/Mukta-Regular.woff2');
@include font('Mukta', normal, 300, '../fonts/Mukta-Light.woff2');
@include font('Mukta', normal, 500, '../fonts/Mukta-Medium.woff2');
@include font('Faktum', normal, 400, '../fonts/Faktum-Regular.woff2');
@include font('Faktum', normal, 600, '../fonts/Faktum-SemiBold.woff2');

// MIXINS
@mixin transition {
  -webkit-transition: all $dur ease;
  transition: all $dur ease;
}

@mixin grid($cols) {
  display: grid;
  grid-template-columns: repeat($cols, 1fr);
  grid-gap: 30px;
}

@mixin placeholderColor($color) {
  &::-webkit-input-placeholder,
  &::-webkit-input-placeholder {
    color: $color;
  }

  &::-moz-placeholder,
  &::-moz-placeholder {
    color: $color;
  }

  &:-moz-placeholder,
  &:-moz-placeholder {
    color: $color;
  }

  &:-ms-input-placeholder,
  &:-ms-input-placeholder {
    color: $color;
  }
}

// BASE - begin
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  font-size: 16px;
  -webkit-text-size-adjust: 100%;
}

body {
  overflow-x: hidden;
  min-width: 320px;
}

body,
input,
textarea,
select {
  font-family: $mf;
  color: $blackColor;
  background-color: transparent;
  border-radius: 0;
}

input,
textarea,
select,
button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  font-size: 16px;
  outline: none;
}

input,
textarea {
  @include placeholderColor($placeholderColor);
}

input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
  color: transparent;
}

input:focus:-moz-placeholder,
textarea:focus:-moz-placeholder {
  color: transparent;
}

input:focus::-moz-placeholder,
textarea:focus::-moz-placeholder {
  color: transparent;
}

input:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {
  color: transparent;
}

select {
  color: $placeholderColor;
}

select option {
  color: $blackColor;

  &:first-child {
    display: none;
  }
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  display: block;
}

// .container {
//   padding: $containerPadding;
// }

.container {
  padding: 0 $containerPadding;
  max-width: $containerWidth;
  width: 100%;
  margin: 0 auto;

  @media (max-width: $lg) {
    padding: 0 16px;
  }
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $sf;
  font-weight: 400;
}

.pt-0 {
  padding-top: 0 !important;
}

// BASE - end
