.main_home {
  .section-hero {
    padding-top: 0;

    .hero {
      height: calc(100vh - 90px);
      position: relative;
      overflow: hidden;
      padding-bottom: 120px;

      &__video {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;

        video {
          min-width: 100%;
          min-height: 100%;
        }

        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          mix-blend-mode: lighten;
          background: linear-gradient(
            123.26deg,
            rgba(0, 0, 96, 0) 30.2%,
            rgba(255, 255, 255, 0) 30.21%,
            #ffb876 115.59%
          );
        }
      }

      &-text {
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        &__small {
          color: #fff;
          font-family: $sf;
          margin-bottom: 30px;
          font-size: 20px;
          line-height: (25/20);
          padding-left: 22px;
          position: relative;

          &::before {
            content: '';
            width: 10px;
            height: 10px;
            background-color: #fff;
            position: absolute;
            left: 0;
            top: 8px;
          }
        }

        &__title {
          font-size: 80px;
          line-height: 1;
          color: #fff;
          font-family: $sf;

          p {
            overflow: hidden;
          }

          span {
            display: block;
          }
        }
      }

      &__scroll-down {
        position: absolute;
        bottom: 53px;
        left: 50%;
        margin-left: -11px;
        cursor: pointer;
        animation: slide 2s ease-in-out infinite;

        svg {
          width: 22px;
          height: 20px;
        }

        @keyframes slide {
          0%,
          100% {
            transform: translateY(0);
          }

          50% {
            transform: translateY(15px);
          }
        }
      }
    }

    @media (max-height: 500px) {
      .hero {
        min-height: 500px;
      }
    }

    @media (max-width: $md) {
      .hero {
        // height: calc(640px - 70px);
        height: calc(100vh - 70px);
        padding-bottom: 109px;

        &-text {
          &__small {
            font-size: 16px;
            margin-bottom: 25px;
            &::before {
              top: 5px;
            }
          }

          &__title {
            font-size: 32px;
            line-height: (38/32);
          }
        }

        &__scroll-down {
          bottom: 39px;
        }
      }
    }
  }

  .section-who {
    .section-body {
      .info-text {
        margin-bottom: 36px;
        border-bottom: 0;
        padding-bottom: 0;
      }

      .styled-btn {
        border-top: 1px solid #d3d3d3;
        padding-top: 36px;
        grid-column: 4/-1;
      }
    }

    @media (max-width: 1024px) {
      .section-body {
        .styled-btn {
          grid-column: 1/-1;
        }
      }
    }

    @media (max-width: $md) {
      padding-top: 64px;

      .section-body {
        .info-text {
          margin-bottom: 24px;
        }
        .styled-btn {
          padding-top: 24px;
        }
      }
    }
  }

  .section-truly-transparent {
    .section-body {
      .info-text {
        margin-bottom: 24px;
      }
      .info-img {
        margin-top: 72px;
      }

      @media (min-width: 993px) {
        .info-text,
        .info-img,
        .styled-btn {
          grid-column: 5/-2;
        }
      }
    }
  }

  .section-what-we-do {
    overflow: hidden;

    .section-heading {
      margin-bottom: 48px;
    }
    .section-body {
      // width: calc(100% + 87px);

      &-in {
        display: grid;
        // grid-template-columns: 411px 1fr;
        grid-template-columns: repeat(12, 1fr);
        // grid-gap: 37px;
      }

      .block-nav {
        padding-top: 48px;
        grid-column: span 3;

        ul {
          li {
            margin-bottom: 16px;
            display: flex;

            &:last-child {
              margin: 0;
            }
          }

          a {
            font-family: $sf;
            font-size: 24px;
            line-height: (34/24);
            opacity: 0.8;
            color: $blackColor;
            transition: all $dur ease;
            position: relative;

            &::before {
              content: '';
              width: 0;
              height: 1px;
              position: absolute;
              top: 15px;
              left: 0;
              background-color: $blackColor;
              transition: width $dur ease;
            }

            &.active,
            &.last {
              font-weight: 600;
              padding-left: 36px;
              opacity: 1;

              &::before {
                width: 24px;
              }

              &:hover {
                color: $blackColor;
              }
            }

            &:hover {
              opacity: 1;
              color: $green;
            }
          }
        }
      }

      .block-cards {
        grid-column: span 9;
        position: relative;
        height: calc(100vh - 240px);
        width: calc((100% * 7) + ((100vw - 20px) * 3) + (10px * 10));

        .card {
          flex-shrink: 0;
          padding: 48px 144px 72px;
          border-radius: 30px;
          display: grid;
          grid-template-columns: repeat(12, 1fr);
          align-items: center;
          float: left;
          height: 100%;
          background-color: #979797;
          transform: translateX(-87px);
          transition: background-color $dur ease;
          position: relative;
          overflow: hidden;

          &:first-child {
            background-color: $blackColor;
            transform: none;
          }

          &:first-child,
          &.active {
            .card__text,
            .card__button,
            .card__img {
              opacity: 1;
              visibility: visible;
              transform: none;
            }
          }

          &__link {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

            &:hover {
              & ~ .card__title span {
                transform: translateY(-5px);
              }
              & ~ .card__button {
                a {
                  color: $blackColor;
                  background-color: $lightGreen;
                  border-color: $lightGreen;
                  padding: 10px 0;
                  .styled-btn__arrow {
                    border-left-color: $blackColor;
                    svg {
                      filter: none;
                    }
                  }
                }
              }
            }
          }

          &__title {
            font-family: $sf;
            color: #fff;
            font-size: 48px;
            line-height: (58/48);
            padding-bottom: 30px;
            border-bottom: 1px solid #97979766;
            grid-column: 1/-1;
            margin-bottom: 102px;

            span {
              display: block;
              transition: transform $dur ease;
            }
          }

          &__img {
            grid-column: 1/-1;
            padding: 0 95px;
            margin-bottom: 91px;
            opacity: 0;
            visibility: hidden;
            transform: translateY(30px);
            transition: all $dur ease;

            img {
              height: 100%;
              max-height: 256px;
            }
          }

          &__text {
            grid-column: 1/8;
            opacity: 0;
            visibility: hidden;
            transform: translateY(20px);
            transition: all $dur ease $dur;

            p {
              color: #ccc;
              font-size: 24px;
              line-height: (34/24);
              margin-bottom: 24px;
              &:last-child {
                margin: 0;
              }
            }
          }

          &__button {
            display: inline-flex;
            grid-column: 9/-1;
            opacity: 0;
            visibility: hidden;
            transform: translateY(10px);
            transition: all $dur ease $dur * 1.2;
          }
        }
      }
    }

    @media (min-width: 1601px) {
      .section-body {
        .block-cards {
          // width: calc((100% * 7) + ((100vw - 20px) * 3) + (10px * 10));

          .card {
            width: 1445px;
          }
        }
      }
    }

    @media (max-width: 1600px) {
      .section-body {
        .block-cards {
          // width: calc((1000px * 7) + ((100vw - 20px) * 3) + (10px * 10));

          .card {
            width: 1110px;
            padding: 48px 72px;

            &__title {
              font-size: 32px;
              padding-bottom: 20px;
              margin-bottom: 48px;
            }

            &__img {
              margin-bottom: 48px;
              img {
                max-height: 156px;
              }
            }

            &__text {
              p {
                font-size: 20px;
              }
            }
          }
        }
      }
    }

    @media (max-width: $xxl), (max-height: $xxlH) {
      .section-body {
        .block-nav {
          ul {
            a {
              font-size: 18px;
            }
          }
        }
      }
    }

    @media (max-width: $xxl) {
      .section-body {
        .block-cards {
          // width: calc((900px * 7) + ((100vw - 20px) * 3) + (10px * 10));

          .card {
            width: 1050px;
          }
        }
      }
    }

    @media (max-width: $xl), (max-height: 700px) {
      overflow: initial;
      .section-body {
        width: auto;

        &-in {
          grid-template-columns: 1fr;
        }

        .block-nav {
          display: none;
        }

        .block-cards {
          width: 100%;
          height: auto;

          .card {
            width: 100%;
            height: auto;
            float: none;
            margin-bottom: 48px;
            background-color: $blackColor;
            transform: none;

            &:last-child {
              margin: 0;
            }

            &__text,
            &__button,
            &__img {
              transition: none;
            }
          }
        }
      }
    }

    @media (max-width: $md) {
      .section-body {
        .block-cards {
          .card {
            padding: 40px 24px;
            border-radius: 16px;
            margin-bottom: 16px;

            &__title {
              font-size: 24px;
              padding-bottom: 11px;
              margin-bottom: 72px;
            }

            &__img {
              margin: 0 auto 42px auto;
              padding: 0 16px;

              img {
                max-height: 174px;
              }
            }

            &__text {
              p {
                font-size: 16px;
                line-height: (22/16);
              }
            }
          }
        }
      }
    }

    @media (max-width: $sm) {
      .section-body {
        .block-cards {
          .card {
            &__text,
            &__button {
              grid-column: 1/-1;
            }

            &__text {
              margin-bottom: 32px;
            }

            &__button {
              display: block;
              .styled-btn {
                a {
                  color: $blackColor;
                  background-color: $lightGreen;
                  border-color: $lightGreen;
                  padding: 10px 0;
                }
                &__arrow {
                  border-left-color: $blackColor;

                  svg {
                    filter: none;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
