.main_text-page {
  margin: 0;
  padding-top: 40px;
  background-color: #e7e7e7;

  .text-page {
    &__heading {
      margin-bottom: 73px;

      .breadcrumbs {
        margin-bottom: 100px;

        &__link {
          color: $blackColor;
        }
        &__current {
          color: #808689;
        }
      }

      .section-title {
        font-size: 48px;
        line-height: (62/48);
        max-width: 1302px;
        overflow: hidden;
        span {
          display: block;
        }
        &::after {
          display: none;
        }
      }

      .back-btn {
        display: none;
      }
    }

    &__body {
      border-top: 1px solid #808689;

      .section-in {
        @include grid(12);
        align-items: flex-start;
      }

      .sidebar {
        grid-column: span 3;
        position: sticky;
        top: 0;
        left: 0;
        padding-top: 48px;
        margin-bottom: 100px;

        &__title {
          font-size: 24px;
          font-family: $sf;
          font-weight: 600;
          margin-bottom: 35px;
          line-height: (36/24);
        }

        &__list {
          max-width: 323px;

          &.is-active {
            .sidebar__item {
              &--accordion {
                .sidebar__anchor {
                  &::after {
                    transform: rotate(180deg);
                  }
                }
              }
            }
          }
        }

        &__item {
          margin-bottom: 16px;

          &--accordion {
            .sidebar__anchor {
              position: relative;
              display: flex;
              align-items: center;
              &::after {
                content: '';
                flex-shrink: 0;
                background-image: url(/img/sprite.svg#chevron);
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
                height: 8px;
                width: 12px;
                margin-left: 12px;
                transition: transform $dur ease;
                transform: rotate(90deg);
              }

              &.active {
                &::after {
                  filter: invert(49%) sepia(39%) saturate(4219%)
                    hue-rotate(58deg) brightness(99%) contrast(104%);
                }
              }
            }
          }

          &:last-child {
            margin: 0;
          }
        }

        &__anchor {
          font-family: $sf;
          font-size: 22px;
          line-height: (31/22);
          position: relative;
          transition: all $dur ease;
          display: block;

          &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 13px;
            height: 2px;
            background-color: $green;
            width: 0;
            transition: width $dur ease;
          }

          &:hover {
            color: $green;
          }

          &.active {
            padding-left: 36px;
            color: $green;

            &::before {
              width: 24px;
            }
          }
        }

        &__sublist {
          margin: 0 0 0 36px;
          overflow: hidden;
          height: 0;
        }

        &__sub-item {
          margin-bottom: 8px;
          &:first-child {
            margin-top: 8px;
          }
          &:last-child {
            margin: 0;
          }
        }

        &__sub-anchor {
          font-size: 20px;
          line-height: (28/20);
          font-weight: 300;
          opacity: 0.8;
          transition: color $dur ease;
          &:hover,
          &.active {
            color: $green;
          }
        }

        a {
          color: $blackColor;
        }
      }

      .content {
        grid-column: 4/-1;
        border-left: 1px solid #808689;

        &-in {
          padding: 48px 0 100px 48px;
        }

        .block {
          margin-bottom: 40px;
          &:last-child {
            margin: 0;
          }

          &__title {
            font-size: 32px;
            font-weight: 600;
            line-height: (48/32);
            margin-bottom: 32px;
          }

          p,
          ul li,
          ol li {
            font-size: 22px;
            font-weight: 300;
          }

          ul,
          ol {
            list-style-position: inside;
            margin-left: 30px;
          }

          ol {
            list-style-type: decimal;
          }

          ul {
            list-style-type: lower-latin;
          }

          ul,
          ol,
          p {
            margin-bottom: 24px;
            &:last-child {
              margin-bottom: 0;
            }
          }

          p {
            a {
              color: $green;
              &:hover {
                text-decoration: underline;
              }
            }
          }
        }

        .qa {
          .qa-block {
            margin-bottom: 32px;
            padding-bottom: 32px;
            border-bottom: 1px solid #808689;

            &:first-child {
              border-top: 1px solid #808689;
              padding-top: 32px;
            }

            &:last-child {
              margin: 0;
            }

            &.is-active {
              .qa-block__title {
                &::before {
                  background-color: $green;
                }
                &::after {
                  transform: translateY(-50%) rotate(0);
                  filter: invert(100%) sepia(0%) saturate(7500%)
                    hue-rotate(32deg) brightness(99%) contrast(150%);
                }
              }
            }

            &__title {
              cursor: pointer;
              font-size: 24px;
              font-weight: 600;
              font-family: $sf;
              line-height: (36/24);
              position: relative;
              padding-right: 118px;

              &::before,
              &::after {
                content: '';
                position: absolute;
                right: 0;
                top: 50%;
                width: 48px;
                height: 48px;
                border-radius: 50%;
                transform: translateY(-50%) rotate(180deg);
                transition: all $dur ease;
              }

              &::before {
                background-color: #ccc;
              }

              &::after {
                background-image: url(/img/sprite.svg#chevron);
                background-repeat: no-repeat;
                background-position: center;
                background-size: 60%;
                width: 48px;
                height: 48px;
              }
            }

            &__text {
              overflow: hidden;
              padding-right: 118px;
            }

            .text-qa {
              &__in {
                margin-top: 32px;
              }
            }
          }
        }
      }
    }
  }

  .section-make-remarkable {
    background-color: #323b40;

    .block-text {
      border-right-color: #808689;
    }

    .block-text p,
    .block-link a {
      color: #e7e7e7;
    }
  }

  @media (max-width: $md) {
    padding-top: 32px;

    .text-page {
      margin-bottom: 40px;

      &__heading {
        margin-bottom: 32px;

        .section-title {
          font-size: 32px;
          line-height: (42/32);
        }

        .breadcrumbs {
          display: none;
        }

        .back-btn {
          display: block;
          margin-bottom: 40px;
        }
      }

      &__body {
        .section-in {
          display: block;
        }

        .sidebar {
          position: static;
          padding-top: 32px;
          margin: 0 0 36px;

          &__title {
            margin-bottom: 16px;
          }

          // &__list {
          //   &.is-active {
          //     .sidebar__item {
          //       &--accordion {
          //         .sidebar__anchor {
          //           color: $green;
          //           &::after {
          //             filter: invert(49%) sepia(39%) saturate(4219%)
          //               hue-rotate(58deg) brightness(99%) contrast(104%);
          //           }
          //         }
          //       }
          //     }
          //   }
          // }

          &__item {
            &--accordion {
              cursor: pointer;

              .sidebar__anchor {
                pointer-events: none;
              }
            }
          }

          &__list {
            max-width: 100%;
          }

          &__anchor {
            font-size: 20px;
            line-height: (28/20);
          }

          &__sub-anchor {
            font-size: 18px;
            line-height: (25/18);
          }
        }

        .content {
          border-left: 0;
          border-top: 1px solid #808689;
          padding-top: 32px;
          &-in {
            padding: 0;
          }

          .block {
            margin-bottom: 32px;

            &__title {
              font-size: 24px;
              line-height: (36/24);
              margin-bottom: 16px;
            }

            p,
            ul li,
            ol li {
              font-size: 20px;
              line-height: (30/20);
            }

            p,
            ul,
            ol {
              margin-bottom: 8px;
            }
          }

          .qa {
            .qa-block {
              margin-bottom: 15px;
              padding-bottom: 15px;

              &__title {
                font-size: 20px;
                line-height: (30/20);
                padding-right: 60px;

                &::before,
                &::after {
                  width: 40px;
                  height: 40px;
                }
              }

              &__text {
                padding: 0;
              }

              .text-qa {
                &__in {
                  margin: 16px 0 9px;
                }
              }

              &:first-child {
                padding-top: 15px;
              }
            }
          }
        }
      }
    }

    .section-make-remarkable {
      .block-text {
        background-color: $lightColor;
        p {
          color: $blackColor;
        }
      }
    }
  }

  @media (max-width: $exSm) {
    .text-page {
      &__body {
        .sidebar {
          &__item {
            &--accordion {
              .sidebar__anchor {
                justify-content: space-between;
              }
            }
          }
        }

        .content {
          .qa {
            .qa-block {
              &__title {
                span {
                  display: block;
                }
              }
            }
          }
        }
      }
    }
  }
}
