.section-img-text {
  .container {
    max-width: 100%;
    padding: 0;
  }

  .img-text {
    @include grid(12);
    grid-gap: 0;

    &__img {
      grid-column: span 5;
      height: 780px;
      min-height: 100%;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transform: scale(1.15);
      }
    }

    &__text {
      grid-column: span 7;
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      padding: 32px 0;
      align-items: center;
      background-color: #ebebeb;

      &--black {
        background-color: $blackColor;

        .text {
          .section-title {
            color: #fff;
          }

          p {
            color: #8a9093;
          }
        }
      }

      .text {
        grid-column: 2/-2;
        max-width: 710px;
        overflow: hidden;
      }

      .section-title {
        margin-bottom: 48px;
      }

      p {
        font-size: 32px;
        line-height: (45/32);
        font-weight: 300;
        margin-bottom: 32px;

        &:last-child {
          margin: 0;
        }
      }
    }
  }

  @media (max-width: $xxl) {
    .img-text {
      &__img {
        height: 580px;
      }
      &__text {
        p {
          font-size: 24px;
        }
      }
    }
  }

  @media (max-width: $md) {
    .img-text {
      &__img,
      &__text {
        grid-column: span 12;
      }

      &__img {
        height: 100%;
        min-height: 367px;
      }

      &__text {
        padding: 48px 16px;

        .text {
          grid-column: span 12;
        }

        .section-title {
          margin-bottom: 36px;
        }

        p {
          font-size: 20px;
          line-height: (30/20);
        }
      }
    }
  }
}
