.section-get-in-touch {
  .section-heading {
    padding-bottom: 34px;
    margin-bottom: 138px;
    position: relative;
    overflow: hidden;

    &__line {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $blackColor;
    }
  }

  .section-body {
    .form {
      .form-span {
        color: red;
        font-size: 14px;
        position: absolute;
        bottom: 0;
        left: 0;
      }

      label {
        font-family: $sf;
      }

      &-group,
      &-checkbox,
      &-submit {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
      }

      &-group {
        margin-bottom: 24px;
        align-items: end;
        overflow: hidden;
        position: relative;

        .help-block {
          position: absolute;
        }
      }

      &-field {
        position: relative;

        &__line {
          position: absolute;
          bottom: 0;
          left: 0;
          background-color: #ababab;
          height: 1px;
          width: 100%;
          display: block;
        }

        label {
          grid-column: 1/6;
          font-size: 32px;
          line-height: (45/32);
          letter-spacing: -0.02em;
          padding-bottom: 13px;
        }

        input,
        textarea {
          border: 1px solid #ababab;
          border-bottom: 0;
          grid-column: 6/-1;
          font-size: 24px;
          letter-spacing: -0.02em;
          color: #828282;
          width: 100%;
          // max-width: 1006px;
        }

        input {
          height: 72px;
          padding: 0 24px;
        }

        textarea {
          height: 72px;
          resize: none;
          padding: 16px 24px;
          align-self: flex-end;
          &::-webkit-scrollbar,
          &-ms-overflow-style {
            display: none;
          }
        }

        &.is-focus {
          input,
          textarea {
            border-color: $blackColor;
            color: $blackColor;
          }

          .form-field__line {
            background-color: $blackColor;
          }
        }
      }

      &-checkbox {
        margin: 48px 0 40px auto;

        &-in {
          grid-column: 6/-1;
          position: relative;
          .form-span {
            bottom: -18px;
            left: 40px;
          }
        }

        input {
          display: none;
        }

        label {
          font-size: 24px;
          line-height: (40/24);
          font-weight: 300;
          position: relative;
          padding-left: 40px;
          max-width: 1006px;
          display: flex;
          font-family: $mf;

          a {
            color: inherit;
            text-decoration: underline;

            &:hover {
              text-decoration: none;
            }
          }
        }
      }

      &-submit {
        width: 100%;
        overflow: hidden;

        button {
          grid-column: 6/-1;
          max-width: 1006px;
          width: 100%;
          height: 64px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-family: $sf;
          font-size: 20px;
          background-color: $lightGreen;
          border: none;
          cursor: pointer;
          position: relative;
          overflow: hidden;
          color: $blackColor;
          z-index: 1;
          transition: color $dur ease;

          &::after {
            content: '';
            background-color: $blackColor;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transform: translateY(100%);
            z-index: -1;
            transition: transform $dur ease;
          }

          &:hover {
            color: #fff;

            &::after {
              transform: none;
            }
          }
        }
      }
    }
  }

  @media (min-width: $xxl) {
    .section-heading {
      .section-title {
        font-size: 80px;
        line-height: (96/80);
      }
    }
  }

  @media (max-width: $lg) {
    .section-body {
      .form {
        &-group {
          label {
            font-size: 28px;
          }
        }
      }
    }
  }

  @media (max-width: $md) {
    .section-heading {
      padding-bottom: 24px;
      margin-bottom: 24px;
      border-bottom-width: 1px;

      .section-title {
        font-size: 40px;
        line-height: (48/40);
      }
    }

    .section-body {
      .form {
        &-group,
        &-submit {
          grid-template-columns: 1fr;
        }

        &-group {
          flex-direction: column;
          margin-bottom: 16px;

          label,
          input,
          textarea {
            font-size: 16px;
            letter-spacing: -0.02em;
            grid-column: auto;
          }

          label {
            padding-bottom: 8px;
          }

          input,
          textarea {
            height: 45px;
            padding: 0 16px;
          }

          textarea {
            padding: 8px 16px;
          }
        }

        &-checkbox {
          margin: 24px 0 24px 16px;

          &-in {
            grid-column: span 12;
          }

          label {
            font-size: 16px;
          }
        }

        &-submit {
          button {
            height: 55px;
            font-size: 16px;
            grid-column: auto;
          }
        }
      }
    }
  }
}
