.section-faq {
  background-color: #283136;

  .section-heading {
    margin-bottom: 48px;
    .section-title {
      color: #fff;
    }
  }

  .section-body {
    .faq {
      &__list {
        margin-bottom: 60px;
      }

      &-item {
        margin-bottom: 40px;
        padding-bottom: 40px;
        @include grid(12);
        grid-row-gap: 0;
        display: none;
        position: relative;

        &__line {
          width: 0;
          height: 1px;
          background-color: #808689;
          position: absolute;
          left: 0;

          &--top {
            top: 0;
            display: none !important;
          }

          &--bottom {
            bottom: 0;
          }
        }

        &:nth-child(-n + 5) {
          display: grid;
        }

        &:last-child {
          margin: 0;
        }

        &:first-child {
          padding-top: 40px;
          .faq-item__line--top {
            display: block !important;
          }
        }

        &__q,
        &__a,
        &__num {
          font-weight: 300;
        }

        &__num {
          font-size: 24px;
          line-height: (36/24);
          color: $lightGreyColor;
          grid-column: span 5;
        }

        &__q {
          color: #fff;
          font-size: 32px;
          line-height: (45/32);
          cursor: pointer;
          position: relative;
          grid-column: 6/-1;
          padding-right: 145px;

          &::after {
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            width: 48px;
            height: 48px;
            border-radius: 50%;
            background-color: #ccc;
            background-image: url(/img/sprite.svg#chevron);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 60%;
            transform: translateY(-50%) rotate(180deg);
            transition: all $dur ease;
          }
        }

        &__a {
          color: #ccc;
          font-size: 24px;
          line-height: (38/24);
          grid-column: 6/-2;
          overflow: hidden;

          &-in {
            margin: 40px 0 20px;
          }

          p {
            margin-bottom: 24px;
            &:last-child {
              margin: 0;
            }
          }

          a {
            color: $lightGreen;
            &:hover {
              text-decoration: underline;
            }
          }
        }

        &.is-active {
          .faq-item__q {
            &::after {
              background-color: $lightGreen;
              transform: translateY(-50%) rotate(0);
            }
          }
        }
      }

      &__button {
        display: flex;
        justify-content: center;

        span {
          background-color: transparent;
          border: 0;
          font-size: 24px;
          line-height: (34/24);
          font-weight: 300;
          letter-spacing: 0.04em;
          color: #ccc;
          cursor: pointer;
        }
      }
    }
  }

  @media (max-width: $xl) {
    .section-body {
      .faq {
        &-item {
          &__num {
            grid-column: span 3;
          }

          &__q {
            grid-column: 4/-1;
          }

          &__a {
            grid-column: 4/-2;
          }
        }
      }
    }
  }

  @media (max-width: $md) {
    .section-heading {
      margin-bottom: 36px;
    }

    .section-body {
      .faq {
        &__list {
          margin-bottom: 32px;
        }

        &-item {
          padding-bottom: 32px;
          margin-bottom: 15px;

          &:first-child {
            padding-top: 15px;
          }

          &__num {
            margin-bottom: 12px;
          }

          &__q {
            padding-right: 60px;

            &::after {
              width: 40px;
              height: 40px;
            }
          }

          &__num,
          &__q,
          &__a {
            grid-column: span 12;
          }

          &__a {
            &-in {
              margin: 20px 0 0 0;
            }
          }

          &__num,
          &__q,
          &__a p {
            font-size: 20px;
            line-height: (30/20);
          }
        }

        &__button {
          span {
            font-size: 20px;
            line-height: (28/20);
          }
        }
      }
    }
  }
}
