.nav {
  position: fixed;
  top: 90px;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 4;
  transition: visibility $dur ease;
  visibility: hidden;

  &__overlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(8, 7, 8, 0.2);
    opacity: 0;
    visibility: hidden;
    transition: all $dur ease;
  }

  &__inner {
    height: 100%;
    max-width: 649px;
    background-color: $lightColor;
    margin-left: auto;
    position: relative;
    overflow: auto;
    transform: translateX(100%);
    transition: transform $dur ease;
  }

  &__list {
    margin-bottom: 80px;
    padding: 80px 72px 80px 160px;
    display: flex;
    flex-direction: column;
  }

  &__item {
    margin-bottom: 40px;

    &:last-child {
      margin: 0;
    }

    a,
    span {
      font-size: 48px;
      line-height: (60/48);
      font-family: $sf;
      color: $blackColor;
      transition: color $dur ease;

      &:hover {
        color: $green;
      }
    }

    &--dropdown {
      & > a,
      & > span {
        cursor: pointer;
        position: relative;
        padding-right: 56px;

        &::after {
          content: '';
          position: absolute;
          right: 0;
          top: 24px;
          background-image: url('/img/sprite.svg#chevron');
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
          width: 32px;
          height: 16px;
          transform: rotate(180deg);
          transition: transform $dur ease;
        }
      }
      &.is-active {
        & > a,
        & > span {
          color: $green;
          &::after {
            transform: none;
          }
        }
      }
    }
  }

  .dropdown {
    overflow: hidden;
    &__list {
      margin-top: 24px;
      padding-left: 40px;
    }

    &__item {
      margin-bottom: 20px;

      &:last-child {
        margin: 0;
      }

      a {
        font-size: 24px;
        opacity: 0.8;
        transition: color $dur ease;
        &:hover {
          color: $green;
        }
      }
    }
  }

  &.active {
    visibility: visible;
    .nav__overlay {
      opacity: 1;
      visibility: visible;
    }

    .nav__inner {
      transform: none;
    }
  }

  @media (max-width: $md) {
    top: 70px;

    &__list {
      padding: 39px 16px;
    }

    &__item {
      text-align: center;
      margin-bottom: 32px;

      &--dropdown {
        & > a,
        & > span {
          padding-right: 37px;

          &::after {
            top: 18px;
            width: 18px;
            height: 9px;
          }
        }
      }

      a,
      span {
        font-size: 32px;
      }
    }

    .dropdown {
      &__list {
        padding: 0;
        margin-top: 16px;
      }
      &__item {
        margin-bottom: 16px;

        a {
          font-size: 20px;
          line-height: (28/20);
        }
      }
    }
  }
}
