.main_contact-page {
  margin: 0;
  padding: 40px 0 140px;
  background-color: #323b40;

  @media (max-width: $md) {
    padding-bottom: 60px;
  }

  .section-contact {
    .section-heading {
      margin-bottom: 60px;

      .breadcrumbs {
        margin-bottom: 110px;
      }

      .section-title {
        color: #fff;
        position: relative;
        overflow: hidden;
        visibility: hidden;
        opacity: 0;
        transform: translateX(50px);

        &__line {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 1px;
          background-color: #808689;
        }

        span {
          display: block;
        }

        &::after {
          transform: translateY(1px);
        }
      }
    }

    .section-body {
      .contact {
        @include grid(12);

        &__img {
          grid-column: span 6;
          overflow: hidden;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }

        &__info {
          grid-column: 8/-1;
          display: flex;
          flex-direction: column;
          justify-content: space-between;

          .info {
            font-family: $sf;

            &__text {
              margin-bottom: 87px;
              font-size: 32px;
              color: $lightColor;
            }

            &__details {
              .detail {
                &__row {
                  overflow: hidden;
                  display: grid;
                  grid-template-columns: repeat(5, 1fr);
                  grid-gap: 30px;
                  padding-bottom: 20px;
                  margin-bottom: 16px;
                  position: relative;

                  &:last-child {
                    margin: 0;
                  }

                  &--address {
                    margin: 58px 0 0 0;
                    .detail__col--value {
                      max-width: 380px;
                    }
                  }

                  &--moving {
                    border: 0;
                    background-color: $blackColor;
                    padding: 36px 30px 43px;

                    .detail__col--name {
                      color: $lightGreen;
                    }

                    .detail__col--value {
                      margin-left: -5px;
                    }
                  }
                }

                &__line {
                  position: absolute;
                  left: 0;
                  bottom: 0;
                  width: 100%;
                  height: 1px;
                  background-color: #808689;
                }

                &__col {
                  font-size: 24px;
                  line-height: (34/24);
                  letter-spacing: 0.02em;

                  &--name {
                    color: $lightColor;
                    grid-column: span 2;
                  }

                  &--value {
                    color: #ccc;
                    grid-column: span 3;

                    a {
                      color: inherit;
                      transition: color $dur ease;

                      &:hover {
                        color: $lightGreen;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    @media (max-width: 1600px) {
      .section-body {
        .contact {
          &__info {
            .info {
              &__text {
                font-size: 24px;
              }

              &__details {
                .detail {
                  &__col {
                    font-size: 20px;
                  }
                }
              }
            }
          }
        }
      }
    }

    @media (max-width: $xl) {
      .section-body {
        .contact {
          &__info {
            grid-column: span 6;
          }
        }
      }
    }

    @media (max-width: $md) {
      .section-heading {
        margin-bottom: 40px;
      }

      .section-body {
        .contact {
          display: block;

          &__img {
            margin-bottom: 24px;
          }

          &__info {
            .info {
              &__text {
                margin-bottom: 40px;
              }
              &__details {
                .detail {
                  &__row {
                    padding-bottom: 16px;
                    &--address {
                      margin-top: 32px;
                      .detail__col--value {
                        max-width: 100%;
                      }
                    }
                    &--moving {
                      padding: 24px 30px 32px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    @media (max-width: $sm) {
      .section-body {
        .contact {
          &__info {
            .info {
              &__details {
                .detail {
                  &__row {
                    &--moving {
                      grid-gap: 12px;
                      .detail__col--name,
                      .detail__col--value {
                        grid-column: 1/-1;
                      }

                      .detail__col--value {
                        margin: 0;
                      }
                    }
                  }

                  &__col {
                    &--name {
                      grid-column: span 1;
                      min-width: 74px;
                    }

                    &--value {
                      grid-column: 2/-1;
                      br {
                        display: none;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .section-get-in-touch {
    padding-top: 126px;

    .section-heading {
      position: relative;

      .section-title {
        color: #fff;
      }

      &__line {
        background-color: #808689;
        bottom: 1px;
      }

      &::after {
        content: '';
        height: 4px;
        width: 64px;
        margin-top: 24px;
        display: block;
        background-color: $lightGreen;
        position: absolute;
        bottom: 0;
        left: 0;
      }
    }

    .section-body {
      .form {
        &-field {
          label,
          input,
          textarea {
            color: $lightColor;
          }

          &__line {
            background-color: #808689 !important;
          }

          input,
          textarea {
            border-color: #808689 !important;
          }

          // &.is-focus {
          //   .form-field__line {
          //     background-color: #fff;
          //   }
          //   input,
          //   textarea {
          //     border-color: #fff;
          //   }
          // }
        }

        &-checkbox {
          label {
            color: #808689;
          }
          .checkbox + .checkbox-label::before {
            background-color: transparent;
            border-color: #808689;
          }
        }
      }
    }

    @media (max-width: $md) {
      padding-top: 60px;
    }
  }
}
