.main {
  margin-bottom: 120px;

  @media (max-width: $md) {
    margin-bottom: 80px;
  }

  .section {
    padding-top: 110px;
    // min-height: 700px;

    &-title {
      &--h1 {
        font-size: 80px;
        line-height: 1;

        &::after {
          content: '';
          height: 3px;
          width: 64px;
          margin-top: 24px;
          display: block;
          background-color: $lightGreen;
        }
      }

      &--h2 {
        font-size: 56px;
        line-height: (67/56);

        &.underline {
          padding-bottom: 36px;
          border-bottom: 1px solid $lightGreyColor;
        }
      }

      &--h4 {
        font-size: 24px;
        line-height: (29/24);
        position: relative;
        padding-left: 48px;

        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 13px;
          width: 32px;
          height: 3px;
          background-color: $green;
        }
      }
    }

    @media (max-width: $md) {
      padding-top: 100px;

      &-title {
        &--h1 {
          font-size: 48px;
          line-height: (58/48);

          &::after {
            margin-top: 16px;
          }
        }

        &--h2 {
          font-size: 32px;
          line-height: (38/32);

          &.underline {
            padding-bottom: 16px;
          }
        }

        &--h4 {
          font-size: 20px;
          line-height: (24/20);

          &::before {
            top: 10px;
          }
        }
      }
    }
  }
}
